"use client";

import React, { useState, useEffect, useCallback } from "react";
import { useAuth } from "@/hooks/useAuth";
import { useRouter, useParams } from "next/navigation";
import { ChevronLeftIcon, DollarLineIcon, TimeIcon, CheckCircleIcon, AlertIcon } from "@/icons";
import { formatCurrency } from "@/utils/format";
import { vendaService, vendaFinalService, type Venda, type VendaFinal } from "@/services/api/venda.service";
import FaturaCard from "@/components/cliente/FaturaCard";
import ResumoFaturas from "@/components/cliente/ResumoFaturas";
import QRCodeModal from "@/components/cliente/QRCodeModal";
import { ThemeToggleButton } from "@/components/common/ThemeToggleButton";
import toast from "react-hot-toast";

export default function CobrancaDetalhesPage() {
  const { user } = useAuth();
  const router = useRouter();
  const params = useParams();
  const [venda, setVenda] = useState<Venda | null>(null);
  const [vendasFinais, setVendasFinais] = useState<VendaFinal[]>([]);
  const [loading, setLoading] = useState(true);
  const [showQRCode, setShowQRCode] = useState(false);
  const [selectedPixCode, setSelectedPixCode] = useState<string>("");
  const [selectedQrCode, setSelectedQrCode] = useState<string>("");

  const loadVenda = useCallback(async () => {
    if (!params.id) return;
    try {
      setLoading(true);
      const vendaId = parseInt(params.id as string);
      
      // Carregar venda
      const vendaResponse = await vendaService.getById(vendaId);
      setVenda(vendaResponse.data);
      
      // Carregar vendas finais relacionadas
      const vendasFinaisResponse = await vendaFinalService.list({
        vendaId: vendaId,
        limit: 100
      });
      const data = vendasFinaisResponse.data;
      setVendasFinais(data);
      console.log('Vendas finais:', data);
    } catch (error) {
      console.error('Erro ao carregar venda:', error);
      toast.error('Erro ao carregar dados da venda');
    } finally {
      setLoading(false);
    }
  }, [params.id]);

  useEffect(() => {
    loadVenda();
  }, [loadVenda]);

  const handleShowQRCode = (pixCode: string, qrCode?: string) => {
    setSelectedPixCode(pixCode);
    setSelectedQrCode(qrCode || "");
    setShowQRCode(true);
  };

  const handleGenerateSuccess = async () => {
    // Recarregar as faturas após gerar uma nova
    await loadVenda();
  };

  const getStatusIcon = (status: string) => {
    switch (status) {
      case 'PAGO':
      case 'APPROVED':
        return <CheckCircleIcon className="w-5 h-5 text-green-500" />;
      case 'PENDENTE':
      case 'PENDING':
        return <TimeIcon className="w-5 h-5 text-yellow-500" />;
      case 'CANCELLED':
        return <AlertIcon className="w-5 h-5 text-red-500" />;
      default:
        return <TimeIcon className="w-5 h-5 text-gray-500" />;
    }
  };

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'PAGO':
      case 'APPROVED':
        return 'bg-green-100 text-green-800 dark:bg-green-900 dark:text-green-300';
      case 'PENDENTE':
      case 'PENDING':
        return 'bg-yellow-100 text-yellow-800 dark:bg-yellow-900 dark:text-yellow-300';
      case 'CANCELLED':
        return 'bg-red-100 text-red-800 dark:bg-red-900 dark:text-red-300';
      default:
        return 'bg-gray-100 text-gray-800 dark:bg-gray-900 dark:text-gray-300';
    }
  };

  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleDateString('pt-BR');
  };

  const formatDateTime = (dateString: string) => {
    return new Date(dateString).toLocaleString('pt-BR');
  };

  if (loading) {
    return (
      <div className="flex items-center justify-center min-h-screen">
        <div className="animate-spin rounded-full h-32 w-32 border-b-2 border-blue-600"></div>
      </div>
    );
  }

  if (!venda) {
    return (
      <div className="text-center py-12">
        <AlertIcon className="w-16 h-16 text-gray-400 mx-auto mb-4" />
        <h3 className="text-lg font-medium text-gray-900 dark:text-white mb-2">
          Venda não encontrada
        </h3>
        <p className="text-gray-600 dark:text-gray-400 mb-4">
          A venda solicitada não foi encontrada ou você não tem permissão para visualizá-la.
        </p>
        <button
          onClick={() => router.push('/dashboard/cliente/cobrancas')}
          className="px-4 py-2 bg-blue-600 hover:bg-blue-700 text-white rounded-lg transition-colors"
        >
          Voltar para Cobranças
        </button>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="bg-white dark:bg-gray-800 rounded-xl border border-gray-200 dark:border-gray-700 p-6">
        <div className="flex items-center justify-between">
          <div className="flex items-center space-x-4">
            <button
              onClick={() => router.back()}
              className="p-2 hover:bg-gray-100 dark:hover:bg-gray-700 rounded-lg transition-colors"
              aria-label="Voltar"
            >
              <ChevronLeftIcon className="w-5 h-5 text-gray-600 dark:text-gray-400" />
            </button>
            <div>
              <h1 className="text-2xl font-bold text-gray-900 dark:text-white">
                Cobrança #{venda.id}
              </h1>
              <p className="text-gray-600 dark:text-gray-400 mt-1">
                Detalhes da cobrança
              </p>
            </div>
          </div>
              <div className="flex items-center space-x-2">
                <ThemeToggleButton />
                {getStatusIcon(venda.status)}
                <span className={`inline-flex items-center px-3 py-1 rounded-full text-sm font-medium ${getStatusColor(venda.status)}`}>
                  {venda.status}
                </span>
              </div>
        </div>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
        {/* Informações da Venda */}
        <div className="lg:col-span-2 space-y-6">
          {/* Dados Principais */}
          <div className="bg-white dark:bg-gray-800 rounded-xl border border-gray-200 dark:border-gray-700 p-6">
            <h3 className="text-lg font-semibold text-gray-900 dark:text-white mb-4">
              Informações da Cobrança
            </h3>
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div>
                <p className="text-sm text-gray-600 dark:text-gray-400">Valor:</p>
                <p className="text-xl font-bold text-gray-900 dark:text-white">
                  {formatCurrency(venda.valor)}
                </p>
              </div>
              <div>
                <p className="text-sm text-gray-600 dark:text-gray-400">Valor Total:</p>
                <p className="text-xl font-bold text-gray-900 dark:text-white">
                  {formatCurrency(venda.valorTotal)}
                </p>
              </div>
              <div>
                <p className="text-sm text-gray-600 dark:text-gray-400">Data de Início:</p>
                <p className="font-medium text-gray-900 dark:text-white">
                  {formatDate(venda.inicio as any)}
                </p>
              </div>
              <div>
                <p className="text-sm text-gray-600 dark:text-gray-400">Data de Fim:</p>
                <p className="font-medium text-gray-900 dark:text-white">
                  {formatDate(venda.fim as any)}
                </p>
              </div>
              <div>
                <p className="text-sm text-gray-600 dark:text-gray-400">Recorrência:</p>
                <p className="font-medium text-gray-900 dark:text-white">
                  {venda.quantidadeRecorrencia > 0 ? `${venda.quantidadeRecorrencia + 1} parcelas` : 'À vista'}
                </p>
              </div>
              <div>
                <p className="text-sm text-gray-600 dark:text-gray-400">Criada em:</p>
                <p className="font-medium text-gray-900 dark:text-white">
                  {formatDateTime(venda.createdAt as any)}
                </p>
              </div>
            </div>
          </div>

          {/* Produtos */}
          {venda.produtos && venda.produtos.length > 0 && (
            <div className="bg-white dark:bg-gray-800 rounded-xl border border-gray-200 dark:border-gray-700 p-6">
              <h3 className="text-lg font-semibold text-gray-900 dark:text-white mb-4">
                Produtos
              </h3>
              <div className="space-y-3">
                {venda.produtos.map((produto) => (
                  <div key={produto.id} className="flex items-center justify-between p-3 bg-gray-50 dark:bg-gray-700 rounded-lg">
                    <div>
                      <p className="font-medium text-gray-900 dark:text-white">
                        {produto.produto.nome}
                      </p>
                      <p className="text-sm text-gray-600 dark:text-gray-400">
                        Quantidade: {produto.quantidade}
                      </p>
                    </div>
                    <div className="text-right">
                      <p className="font-medium text-gray-900 dark:text-white">
                        {formatCurrency(produto.valorTotal)}
                      </p>
                      <p className="text-sm text-gray-600 dark:text-gray-400">
                        {formatCurrency(produto.valorUnitario)} cada
                      </p>
                    </div>
                  </div>
                ))}
              </div>
            </div>
          )}

          {/* Faturas */}
          <div className="bg-white dark:bg-gray-800 rounded-xl border border-gray-200 dark:border-gray-700 p-6">
            <h3 className="text-lg font-semibold text-gray-900 dark:text-white mb-4">
              Faturas ({vendasFinais.length})
            </h3>
            {vendasFinais.length === 0 ? (
              <p className="text-gray-600 dark:text-gray-400 text-center py-4">
                Nenhuma fatura encontrada para esta cobrança.
              </p>
            ) : (
              <div className="space-y-4">
                {vendasFinais.map((vendaFinal) => (
                  <FaturaCard
                    key={vendaFinal.id}
                    vendaFinal={vendaFinal}
                    onShowQRCode={handleShowQRCode}
                    onGenerateSuccess={handleGenerateSuccess}
                  />
                ))}
              </div>
            )}
          </div>
        </div>

        {/* Sidebar */}
        <div className="space-y-6">
          {/* Resumo das Faturas */}
          <ResumoFaturas 
            vendasFinais={vendasFinais}
            valorTotal={venda.valorTotal}
          />

          {/* Informações do Vendedor */}
          <div className="bg-white dark:bg-gray-800 rounded-xl border border-gray-200 dark:border-gray-700 p-6">
            <h3 className="text-lg font-semibold text-gray-900 dark:text-white mb-4">
              Vendedor
            </h3>
            <div className="space-y-2">
              <p className="font-medium text-gray-900 dark:text-white">
                {venda.vendedor?.nome || 'N/A'}
              </p>
              <p className="text-sm text-gray-600 dark:text-gray-400">
                ID: {venda.vendedor?.id || 'N/A'}
              </p>
            </div>
          </div>

          {/* Informações da Empresa */}
          <div className="bg-white dark:bg-gray-800 rounded-xl border border-gray-200 dark:border-gray-700 p-6">
            <h3 className="text-lg font-semibold text-gray-900 dark:text-white mb-4">
              Empresa
            </h3>
            <div className="space-y-2">
              <p className="font-medium text-gray-900 dark:text-white">
                {venda.empresa?.nome || 'N/A'}
              </p>
              <p className="text-sm text-gray-600 dark:text-gray-400">
                ID: {venda.empresa?.id || 'N/A'}
              </p>
            </div>
          </div>
        </div>
      </div>

      {/* Modal QR Code */}
      <QRCodeModal
        isOpen={showQRCode}
        onClose={() => setShowQRCode(false)}
        pixCode={selectedPixCode}
        qrCode={selectedQrCode || ""}
      />
    </div>
  );
}
