import React from 'react';
import { VendaFinal } from '@/services/api/venda.service';
import { formatCurrency } from '@/utils/format';
import { CheckCircleIcon, TimeIcon, AlertIcon } from '@/icons';

interface ResumoFaturasProps {
  vendasFinais: VendaFinal[];
  valorTotal: number;
}

const ResumoFaturas: React.FC<ResumoFaturasProps> = ({
  vendasFinais,
  valorTotal
}) => {
  const totalParcelas = vendasFinais.length;
  const parcelasPagas = vendasFinais.filter(vf => vf.status === 'APPROVED').length;
  const parcelasPendentes = vendasFinais.filter(vf => vf.status === 'PENDING').length;
  const parcelasCanceladas = vendasFinais.filter(vf => vf.status === 'CANCELLED').length;
  
  const valorPorParcela = totalParcelas > 0 ? valorTotal / totalParcelas : 0;
  const valorTotalPago = vendasFinais
    .filter(vf => vf.status === 'APPROVED')
    .reduce((sum, vf) => sum + parseFloat(vf.valor.toString()), 0);
  const valorTotalPendente = vendasFinais
    .filter(vf => vf.status === 'PENDING')
    .reduce((sum, vf) => sum + parseFloat(vf.valor.toString()), 0);
  const valorTotalCancelado = vendasFinais
    .filter(vf => vf.status === 'CANCELLED')
    .reduce((sum, vf) => sum + parseFloat(vf.valor.toString()), 0);

  return (
    <div className="bg-white dark:bg-gray-800 rounded-xl border border-gray-200 dark:border-gray-700 p-6">
      <h3 className="text-lg font-semibold text-gray-900 dark:text-white mb-4">
        Resumo das Faturas
      </h3>
      
      <div className="space-y-4">
        {/* Estatísticas Gerais */}
        <div className="grid grid-cols-2 gap-4">
          <div className="text-center p-3 bg-gray-50 dark:bg-gray-700 rounded-lg">
            <p className="text-sm text-gray-600 dark:text-gray-400">Total de Parcelas</p>
            <p className="text-xl font-bold text-gray-900 dark:text-white">{totalParcelas}</p>
          </div>
          <div className="text-center p-3 bg-gray-50 dark:bg-gray-700 rounded-lg">
            <p className="text-sm text-gray-600 dark:text-gray-400">Valor por Parcela</p>
            <p className="text-xl font-bold text-gray-900 dark:text-white">{formatCurrency(valorPorParcela)}</p>
          </div>
        </div>

        {/* Status das Parcelas */}
        <div className="space-y-3">
          <div className="flex items-center justify-between p-3 bg-green-50 dark:bg-green-900/20 rounded-lg">
            <div className="flex items-center space-x-2">
              <CheckCircleIcon className="w-6 h-6 text-green-500" />
              <span className="text-sm font-medium text-green-800 dark:text-green-300">Parcelas Pagas</span>
            </div>
            <div className="text-right">
              <p className="text-lg font-bold text-green-800 dark:text-green-300">{parcelasPagas}</p>
              <p className="text-sm text-green-600 dark:text-green-400">{formatCurrency(valorTotalPago)}</p>
            </div>
          </div>

          <div className="flex items-center justify-between p-3 bg-yellow-50 dark:bg-yellow-900/20 rounded-lg">
            <div className="flex items-center space-x-2">
              <TimeIcon className="w-6 h-6 text-yellow-500" />
              <span className="text-sm font-medium text-yellow-800 dark:text-yellow-300">Parcelas Pendentes</span>
            </div>
            <div className="text-right">
              <p className="text-lg font-bold text-yellow-800 dark:text-yellow-300">{parcelasPendentes}</p>
              <p className="text-sm text-yellow-600 dark:text-yellow-400">{formatCurrency(valorTotalPendente)}</p>
            </div>
          </div>

          {parcelasCanceladas > 0 && (
            <div className="flex items-center justify-between p-3 bg-red-50 dark:bg-red-900/20 rounded-lg">
              <div className="flex items-center space-x-2">
                <AlertIcon className="w-6 h-6 text-red-500" />
                <span className="text-sm font-medium text-red-800 dark:text-red-300">Parcelas Canceladas</span>
              </div>
              <div className="text-right">
                <p className="text-lg font-bold text-red-800 dark:text-red-300">{parcelasCanceladas}</p>
                <p className="text-sm text-red-600 dark:text-red-400">{formatCurrency(valorTotalCancelado)}</p>
              </div>
            </div>
          )}
        </div>

        {/* Totais */}
        <div className="border-t border-gray-200 dark:border-gray-600 pt-4">
          <div className="grid grid-cols-2 gap-4">
            <div className="text-center">
              <p className="text-sm text-gray-600 dark:text-gray-400">Valor Total</p>
              <p className="text-lg font-bold text-gray-900 dark:text-white">{formatCurrency(valorTotal)}</p>
            </div>
            <div className="text-center">
              <p className="text-sm text-gray-600 dark:text-gray-400">Progresso</p>
              <div className="mt-1">
                <div className="w-full bg-gray-200 dark:bg-gray-700 rounded-full h-2">
                  <div 
                    className={`bg-green-500 h-2 rounded-full transition-all duration-300`}
                    style={{
                      width: totalParcelas > 0 ? `${Math.round((parcelasPagas / totalParcelas) * 100)}%` : '0%'
                    }}
                  ></div>
                </div>
                <p className="text-xs text-gray-600 dark:text-gray-400 mt-1">
                  {totalParcelas > 0 ? Math.round((parcelasPagas / totalParcelas) * 100) : 0}% concluído
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ResumoFaturas;
